import { LimitOrderInfo } from '@/types/limit'
import { formatNumber, formatPercentage } from '@cetus/utils'
import { HStack, Text } from '@chakra-ui/react'

export const FilledSizeBlock = ({ info }: { info: LimitOrderInfo }) => {
  const { pay_coin, total_pay_amount, deal_amount, deal_rate } = info
  return (
    <HStack justifyContent="end" gap="2px">
      <Text color="text_caption">
        {formatNumber(deal_amount)}/{formatNumber(total_pay_amount)} {pay_coin.symbol}
      </Text>
      <Text color="primary_gray">({formatPercentage(Number(deal_rate) * 100)})</Text>
    </HStack>
  )
}
